import { LSK_TCB_ENV_ID } from '../constants/storageKeyNames';

export function noop() { return false };

export function getCachedEnvId() {
    return window.localStorage.getItem(LSK_TCB_ENV_ID) || '';
}
export function setCacheEnvId(val) {
    return window.localStorage.setItem(LSK_TCB_ENV_ID, val);
}

export function guid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}