import "babel-polyfill"

import React, { useContext, useEffect, useState } from 'react';
import { getCachedEnvId, setCacheEnvId, noop, guid } from './utils/index';
import cloudbase from '@cloudbase/js-sdk';
import { TcbAppContext, TcbLoginStateContext } from './constants/context';
import { ASYNC_FETCH_OPERATION_STATUS } from './constants/index';

const cachedEnvId = getCachedEnvId();

export default function App() {
    const [envId, setEnvId] = useState(cachedEnvId);
    if (!envId) {
        return (
            <RequireEnvironmentIdComponent handleSetCacheEnvId={() => {
                const id = window.prompt('Please input the tcb environment id') || '';
                if (!id) { window.alert('Invalid id, process canceled'); return; }
                setEnvId(id);
                setCacheEnvId(id);
            }} />
        );
    } else {
        return (
            <TcbContextProvider envId={envId} />
        );
    }
}

function RequireEnvironmentIdComponent(props) {
    const { handleSetCacheEnvId = noop } = props;
    return (
        <button onClick={handleSetCacheEnvId}>set environment id</button>
    );
}

function TcbContextProvider(props) {
    const { envId } = props;
    const [app, setApp] = useState(null);
    useEffect(() => {
        const tcb = cloudbase.init({
            env: envId || "social-4gnt1iwk3d0bc8b4",
        });
        setApp(tcb);
    }, []);
    if (!app) return null;
    return (
        <TcbAppContext.Provider value={app}>
            <p>hello react 5</p>
            <TcbLoginStateProvider />
        </TcbAppContext.Provider>
    );
}

const initLogStateFetchVersion = guid();
function TcbLoginStateProvider() {
    const appContext = useContext(TcbAppContext);
    if (!appContext) return null;
    const [fetchError, setFetchError] = useState(null);
    const [fetchLoginStateStatus, setFetchLoginStateStatus] = useState(ASYNC_FETCH_OPERATION_STATUS.INIT);
    const [state, setState] = useState(null);
    const [fetchVersion, setfetchVersion] = useState(initLogStateFetchVersion);
    useEffect(() => {
        async function main() {
            try {
                setFetchError(null);
                setFetchLoginStateStatus(ASYNC_FETCH_OPERATION_STATUS.PENDING);
                console.log('appContext', appContext);
                console.log('appContext.getLoginState', appContext.getLoginState);
                const auth = appContext.auth({
                    persistence: "local" // 用户显式退出或更改密码之前的30天一直有效
                });
                const loginState = await auth.getLoginState();
                setState(loginState);
                setFetchLoginStateStatus(ASYNC_FETCH_OPERATION_STATUS.SUCCESS);
            } catch (err) {
                setFetchError(err);
                setFetchLoginStateStatus(ASYNC_FETCH_OPERATION_STATUS.FAIL);
            }
        };
        main();
    }, [fetchVersion]);
    switch (fetchLoginStateStatus) {
        case ASYNC_FETCH_OPERATION_STATUS.INIT: {
            return (
                <p>init</p>
            );
        };
        case ASYNC_FETCH_OPERATION_STATUS.PENDING: {
            return (
                <p>pending</p>
            );
        };
        case ASYNC_FETCH_OPERATION_STATUS.SUCCESS: {
            if (!state) return (
                <>
                    <p>用户未登录</p>
                    <EmailAndPasswordLoginForm reload={() => {
                        setfetchVersion(guid());
                    }} />
                </>
            );
            return (
                <TcbLoginStateContext.Provider value={state} >
                    <AppWithTcbAndUserContext />
                </TcbLoginStateContext.Provider>
            );
        };
        case ASYNC_FETCH_OPERATION_STATUS.FAIL: {
            console.log('fetchError', fetchError);
            return (
                <>
                    <p>failed:</p>
                    <p>{fetchError.message}</p>
                </>
            );
        };
        case ASYNC_FETCH_OPERATION_STATUS.INIT:
        default: {
            return (
                <>
                    <p>hi</p>
                </>
            );
        }
    }
}

function AppWithTcbAndUserContext() {
    const loginState = useContext(TcbLoginStateContext);
    if (!loginState) return null;
    console.log('loginState', loginState);
    return (
        <>
            <p>success:</p>
            <ol>
                <li>loginType: {loginState.loginType}</li>
                <li>isAnonymousAuth: {loginState.isAnonymousAuth ? 'true' : 'false'}</li>
                <li>nickName: {loginState.user.nickName | '-'}</li>
                <li>email: {loginState.user.email}</li>
                <li>hasPassword: {loginState.user.hasPassword ? 'true' : 'false'}</li>
            </ol>
        </>
    );
}

function EmailAndPasswordLoginForm(props) {
    const { reload = noop } = props;
    const appContext = useContext(TcbAppContext);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [fetchError, setFetchError] = useState(null);
    const [loginStatus, setLoginStatus] = useState(ASYNC_FETCH_OPERATION_STATUS.INIT);
    return (
        <div>
            <input type="email" value={email} onChange={evt => setEmail(evt.target.value)} placeholder="please input email" />
            <br />
            <input type="password" value={password} onChange={evt => setPassword(evt.target.value)} placeholder="please input password" />
            <br />
            {fetchError && <p>{fetchError.message}</p>}
            <br />
            <button onClick={async () => {
                if (!email) {
                    alert('invalid emial');
                    return;
                } else if (!password) {
                    alert('invalid password');
                    return;
                } else {
                    const auth = appContext.auth({
                        persistence: "local" // 用户显式退出或更改密码之前的30天一直有效
                    });
                    try {
                        setFetchError(null);
                        setLoginStatus(ASYNC_FETCH_OPERATION_STATUS.PENDING);
                        const loginState = await auth.signInWithEmailAndPassword(email, password);
                        console.log('loginState', loginState);
                        setLoginStatus(ASYNC_FETCH_OPERATION_STATUS.SUCCESS);
                        reload();
                    } catch (err) {
                        setFetchError(new Error('auth failed'));
                        setLoginStatus(ASYNC_FETCH_OPERATION_STATUS.FAIL);
                    }
                }
            }} disabled={loginStatus === ASYNC_FETCH_OPERATION_STATUS.PENDING}>submit</button>
        </div>
    );
}